const showcategory = ({ id }) => {
  if (id === "web-developer") {
    return <Frontend />;
  } else if (id === "career") {
    return <Seminor />;
  } else if (id === "cms") {
    return <Cms />;
  } else if (id === "seo") {
    return <Seo />;
  } else if (id === "overseas-freelancing") {
    return <Nomado />;
  } else if (id === "ginneko-tsuredure") {
    return <Lifehack />;
  }
};

export default showcategory;
